export default {
  name: "AirdropAwardCard",
  props: {
    showAirdropAward: {
      type: Boolean,
      default: false
    },
    currentAddress: {
      type: String,
      default: ''
    },
    luckNum: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      MintNum: 0,
      visables: this.showAirdropAward,
    }
  },
  watch: {
    showAirdropAward(n) {
      this.visables = n;
    },
  },
  methods: {
    close() {
      this.$emit("closeAirdropAwardCard", false);
    }
  }
}

